import React from "react";
import Checkbox from "../../../designSystem/Checkbox";
import handleEvent from '../../../helpers/event-utils';


export default function CheckboxComponent(props) {
  const {getPConnect, value=false, readOnly, required=false, helperText} = props;

  const thePConn = getPConnect();
  const theConfigProps = thePConn.getConfigProps();
  const { caption } = theConfigProps;
  const actionsApi = thePConn.getActionsApi();
  const propName = thePConn.getStateProps().value;

  const handleChange = (event) => {
    handleEvent(actionsApi, "changeNblur", propName, event.target.checked);
  }

  const handleBlur = (event) => {
    thePConn.getValidationApi().validate(event.target.checked);
  }

  return <Checkbox 
    helperText={helperText}
    value={value || false}
    onChange={handleChange} 
    onBlur={handleBlur}
    label={caption}
    labelPlacement="end" 
    readOnly={readOnly} 
    required={required}/>;

}
