import React from "react";

export interface CheckboxProps {
    label: string;  // eslint-disable-line react/no-unused-prop-types
    helperText: string;  // eslint-disable-line react/no-unused-prop-types
    labelPlacement?: 'start' | 'end';  // eslint-disable-line react/no-unused-prop-types
    value?: string;  // eslint-disable-line react/no-unused-prop-types
    required?: boolean; // eslint-disable-line react/no-unused-prop-types
    readOnly?: boolean; // eslint-disable-line react/no-unused-prop-types
    onChange?: (e: React.ChangeEvent) => void; // eslint-disable-line react/no-unused-prop-types
    onBlur?: (e: React.ChangeEvent) => void; // eslint-disable-line react/no-unused-prop-types
}

export default function Checkbox(props: CheckboxProps) {
    return <label className="checkbox">
        <input type="checkbox" 
            required={props.required}
            disabled={props.readOnly}
            value={props.value}
            onChange={e => (props as any).onChange(e)}/>
        {props.label}
        <span>{props.helperText}</span>
    </label>;
}
