import React from "react";

import TextInput from "../TextInput";
import TextField from "../../../designSystem/TextField";


export default function Email(props) {
  const {label, required, disabled, value='', validatemessage, onChange, onBlur, readOnly} = props;

  if (readOnly) {
    return ( <TextInput {...props} /> );
  }

  return (
      <TextField
        helperText={validatemessage}
        placeholder=""
        required={required}
        disabled={disabled}
        onChange={onChange}
        onBlur={onBlur}
        label={label}
        value={value}
        type="email"
      />
    )
}
