import React from "react";
// import "./button.scss";

export interface ButtonProps {
    variant: string;  // eslint-disable-line react/no-unused-prop-types
    color: string; // eslint-disable-line react/no-unused-prop-types
    component?: string; // eslint-disable-line react/no-unused-prop-types
    onClick?: (e: React.MouseEvent) => void;
    children: React.ReactNode;
}

export default function Button(props: ButtonProps) {
    return <button type="button" className={`button ${props.color}`} onClick={e => props.onClick?props.onClick(e):null}>{props.children}</button>;
}
