import React from "react";

export interface KeyboardDatePickerProps {
    placeholder: string;  // eslint-disable-line react/no-unused-prop-types
    format: string;  // eslint-disable-line react/no-unused-prop-types
    mask: string;  // eslint-disable-line react/no-unused-prop-types
    helperText: string;  // eslint-disable-line react/no-unused-prop-types
    label: string;  // eslint-disable-line react/no-unused-prop-types
    value: string;  // eslint-disable-line react/no-unused-prop-types
    required: boolean; // eslint-disable-line react/no-unused-prop-types
    disabled: boolean; // eslint-disable-line react/no-unused-prop-types
    onChange: (e: React.ChangeEvent) => void; // eslint-disable-line react/no-unused-prop-types
}

export default function KeyboardDatePicker(props: KeyboardDatePickerProps) {
    return <label>{props.label}
            <input type="date" 
                required={props.required}
                disabled={props.disabled}
                value={props.value || ''}
                onChange={e => props.onChange(e)}
                />
        {/* <span>{props.helperText}</span> */}
    </label>;
}
