import React from "react";
import KeyboardDatePicker from "../../../designSystem/KeyboardDatePicker";

import TextInput from "../TextInput";

export default function Date(props) {
  const {label, required, disabled, value='', validatemessage, onChange, readOnly} = props;

  if (readOnly) {
    // const theReadOnlyComp = <TextInput props />
    return ( <TextInput {...props} /> );
  }

  const handleChange = date => {
    const dateObj = new window.Date(date.target.value);
    onChange({value: dateObj.toISOString().split('T')[0]});
  }

  return (
      <KeyboardDatePicker
        placeholder="mm/dd/yyyy"
        required={required}
        disabled={disabled}
        format="MM/DD/YYYY"
        mask="__/__/____"
        helperText={validatemessage}
        label={label}
        value={value || null}
        onChange={handleChange}
      />
    );
}
