import React, {useEffect, useState} from "react";
import Utils from "../../../helpers/utils";
import TextField from "../../../designSystem/TextField";

interface IOption {
  key: string;
  value: string;
}

export default function Dropdown(props) {
  const {getPConnect, label, required, disabled, placeholder, value='', datasource=[], validatemessage, onChange, readOnly} = props;
  const [options, setOptions] = useState<Array<IOption>>([]);

  useEffect(() => {
    const optionsList = Utils.getOptionList(props, getPConnect().getDataObject());
    if (!optionsList.find(option => option.key === 'Select')) {
      optionsList.unshift({key: 'Select', value: 'Select...'});
    }
    setOptions(optionsList);
  }, [datasource]);

  const handleChange = evt => {
    if (evt.target.value === 'Select') {
      onChange({value: ''});
    } else {
      onChange({value: evt.target.value});
    }
  }

  // Material UI shows a warning if the component is rendered before options are set.
  //  So, hold off on rendering anything until options are available...
  return (
    options.length === 0 ?
      null
      :
      <TextField
        helperText={validatemessage}
        placeholder={placeholder}
        required={required}
        disabled={disabled}
        readOnly={readOnly}
        onChange={handleChange}
        onBlur={handleChange}
        label={label}
        value={(value === '' && !readOnly) ? 'Select' : value}
        select
      >
        {
          options.map((option: any) => (
              <option
                key={option.key}
                value={option.key}
              >
                {option.value}
              </option>
            ))
        }
      </TextField>
    );
}
