import React from "react";
import TextField from "../../../designSystem/TextField";

export default function TextInput(props) {
  const {label, required, disabled, value='', validatemessage, helperText, status, onChange, onBlur, readOnly} = props;

  return (
      <TextField
        helperText={status === 'error' ? validatemessage : helperText}
        placeholder=""
        error={status === 'error'}
        required={required}
        disabled={disabled}
        readOnly={readOnly}
        onChange={onChange}
        onBlur={onBlur}
        label={label}
        value={value}
      />
    )
}
