import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faEnvelope, faQuestionCircle, faCircleQuestion } from '@fortawesome/free-regular-svg-icons'
import { faAngleDown, faSearch } from '@fortawesome/free-solid-svg-icons'
import React, { Component, Children } from "react";

export interface TextFieldProps {
    placeholder?: string;
    helperText?: string;
    label?: string;
    type?: string;
    value?: string;
    required?: boolean;
    disabled?: boolean;
    readOnly?: boolean;
    select?: boolean;
    error?: boolean;
    id?: string;
    onBlur?: (e: React.MouseEvent) => void;
    onChange?: (e: React.ChangeEvent) => void;
    children?: React.ReactNode;
}

interface TextFieldState {
    value: string | undefined;
}

export default class TextField extends Component<TextFieldProps, TextFieldState> {
    constructor(props: TextFieldProps) {
        super(props);
        this.state = {value: props.value || ''};
    }

    private handleChange(e) {
        this.setState({value: e.target.value});
        if(this.props.onChange) this.props.onChange(e);
    }

    render() {
        return <label id={this.props.id} className={`input-label${this.props.required ? ' required' : ''}${this.props.error ? ' invalid' : ''}`}>
            <span>{this.props.label}</span> {this.props.helperText && !this.props.error ? <span title={this.props.helperText}><FontAwesomeIcon icon={faCircleQuestion} /></span> : null}
            {
                this.props.select ? 
                    <select
                        required={this.props.required}
                        disabled={this.props.disabled}
                        value={this.state.value}
                        onChange={e => this.handleChange(e)}>
                        {this.props.children}
                    </select> :
                    <input type={this.props.type || 'text'}
                        required={this.props.required}
                        disabled={this.props.disabled}
                        readOnly={this.props.readOnly}
                        placeholder={this.props.placeholder}
                        value={this.state.value}
                        onChange={e => this.handleChange(e)}/>
            }
            {
                this.props.select || (this.props.type && ['email', 'search'].indexOf(this.props.type) >= 0) ?
                <div className="input-icon">
                    {this.props.select ? 
                        <FontAwesomeIcon icon={faAngleDown} /> :
                        this.props.type === 'email' ? <FontAwesomeIcon icon={faEnvelope} /> :
                        this.props.type === 'search' ? <FontAwesomeIcon icon={faSearch} /> : null
                    }
                </div> : null 
            }
            {this.props.helperText && this.props.error ? <span className="helperText">{this.props.helperText}</span> : null}
        </label>
    }
}
